import Api from './baseApi'

export default class adminApi extends Api {
    static adminList(data, callback, errorBack) {
        super.request(this.uri() + 'entry', callback, errorBack, data)
    }

    static create(data, callback, errorBack) {
        super.request(this.uri() + 'create', callback, errorBack, data)
    }

    static detail(data, callback, errorBack) {
        super.request(this.uri() + 'detail', callback, errorBack, data)
    }

    static spinner(callback, errorBack) {
        super.request(this.uri() + 'spinner', callback, errorBack)
    }

    static assignUserRole(data, callback, errorBack){
        super.request(this.uri() + 'assign_user_role', callback, errorBack, data)

    }

    static pwd(data, callback, errorBack){
        super.request(this.uri() + 'change_password', callback, errorBack, data)

    }
    static info(callback, errorBack){
        super.request(this.uri() + 'info', callback, errorBack)

    }
    static uri() {
        return super.url() + 'admin/'
    }

}
