<style scoped lang="less">
    .pages {
        text-align: left;
        padding: 0 20px;
        .form {
            width: 40%;
            margin-top: 30px;
            margin-left: 80px;
        }
    }

</style>


<template>
    <div class="pages">
        <H3>修改密码</H3>
        <div class="line"></div>
        <div class="form"></div>
        <el-form class="form" ref="form" :model="form" label-width="120px">
            <el-row>
                <el-form-item label="新密码" prop="pwd">
                    <el-input v-model="form.password" type="password" auto-complete="off"
                              placeholder="请输入"></el-input>
                </el-form-item>
                <el-form-item label="确认新密码" prop="pwd">
                    <el-input v-model="form.confirm_password" type="password" auto-complete="off"
                              placeholder="请输入"></el-input>
                </el-form-item>
            </el-row>
            <el-button style="margin-left: 120px;width: 40%" type="primary" :loading="isLoading" @click="onSubmit">提交
            </el-button>
        </el-form>
    </div>
</template>

<script>
    import api from '../../js/api/admin'

    export default {
        name: "updatePass",
        data() {
            return {
                form: {
                    password: '',
                    confirm_password: ''
                },
                isLoading: false,
            }
        },
        methods: {
            onSubmit() {
                this.isLoading = true;
                api.pwd(this.form, () => {
                    this.isLoading = false;
                    this.$message.success('修改密码成功');
                    this.$router.push({name: 'login'})
                },()=>{
                    this.isLoading = false;
                })

            }
        }
    }
</script>
